import Vue from "vue";
import Router from "vue-router";
/* Layout */
import Layout from "@/views/layout/Layout";

Vue.use(Router);
/* Router Modules */
import mainRoutes from "@/router/modules/example";
import systemRoute from "@/router/modules/system";
import basicInfo from "@/router/modules/basicInfo";
import physicalManage from "@/router/modules/physicalManage";
import oilStationManage from "@/router/modules/oilStationManage";
/** note: Submenu only appear when children.length>=1
 *  detail see  https://panjiachen.github.io/vue-element-admin-site/guide/essentials/router-and-nav.html
 **/

/**
 * hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
 * alwaysShow: true               if set true, will always show the root menu, whatever its child routes length
 *                                if not set alwaysShow, only more than one route under the children
 *                                it will becomes nested mode, otherwise not show the root menu
 * redirect: noredirect           if `redirect:noredirect` will no redirect in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    roles: ['admin','editor']     will control the page roles (you can set multiple roles)
    title: 'title'               the name show in submenu and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar,
    noCache: true                if true ,the page will no be cached(default is false)
  }
 **/

export const constantRouterMap = [
  {
    path: "/login",
    component: () => import("@/views/login/index"),
    hidden: true
  },
  {
    path: "/",
    component: Layout,
    children: [...mainRoutes],
    meta: { title: "example", icon: "icon icon-shezhi" }
  },
  {
    path: "/404",
    component: () => import("@/views/errorPage/404"),
    hidden: true
  },
  {
    path: "/401",
    component: () => import("@/views/errorPage/401"),
    hidden: true
  }
];

export const asyncRouterMap = [
  systemRoute,
  basicInfo,
  physicalManage,
  oilStationManage,
  {
    path: "*",
    redirect: "/404"
  }
];

export default new Router({
  mode: "history",
  scrollBehavior: () => ({ y: 0 }),
  routes: [...constantRouterMap, ...asyncRouterMap]
});
